import axios from 'axios'

const venadosDacodes = axios.create({
  timeout: 10000,
  headers: {
    'Accept': 'application/json',
  }
})

export default {
  fetchGames(){
    return venadosDacodes.get('https://raw.githubusercontent.com/ajcarrillo/venados-challenge/master/data/games.json');
  },
  fetchStatistics(){
    return venadosDacodes.get('https://raw.githubusercontent.com/ajcarrillo/venados-challenge/master/data/statistics.json')
  },
  fetchPlayers(){
    return venadosDacodes.get('https://raw.githubusercontent.com/ajcarrillo/venados-challenge/master/data/players.json')
  }
}